import { useEffect, useState } from 'react';

export const useCurrentTimeHook = () => {
  const [currentTime, setCurrentTime] = useState({
    time: '',
    date: '',
  });

  useEffect(() => {
    const updateTime = () => {
      const now = new Date();
      const hours = now.getHours();
      const minutes = now.getMinutes();
      const ampm = hours >= 12 ? 'PM' : 'AM';
      const formattedTime = `${hours % 12 || 12}:${String(minutes).padStart(2, '0')} ${ampm}`;
      const formattedDate = `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, '0')}-${String(
        now.getDate()
      ).padStart(2, '0')}`;
      setCurrentTime({
        time: formattedTime,
        date: formattedDate,
      });
    };

    updateTime(); // Ï¥àÍ∏∞ Ïã§Ìñâ
    const interval = setInterval(updateTime, 60000); // Îß§ Î∂ÑÎßàÎã§ Í∞±Ïã†

    return () => clearInterval(interval); // Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Ïù∏ÌÑ∞Î≤å Ï†ïÎ¶¨
  }, []);

  return currentTime;
};
